body {
    background: #f0f0f0;
    font-family: impact, sans-serif;
    font-size: 16px;
}

#root {
    display: flex;
    height: 100vh;
    align-items: center;
}

#root > div {
    max-width: 600px;
    width: 90%;
    margin: 0 auto;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 30px -15px rgba(0,0,0,0.4)
}

header {
    display: flex;
    align-items: center;
    padding: 30px 40px;
}

header img.logo {
    max-width: 120px;
    margin-right: 15px;
}

header .logoText {
    font-size: 2.5rem;
    font-weight: 900;
    text-transform: uppercase;
}

main .memeApp .memeRender {
    position: relative;
}

main .memeApp .memeRender img {
    width: 100%;
    max-height: 70vh;
}

main .memeApp .memeRender h2 {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 3.5em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        4px 4px 0 #000,
        -4px -4px 0 #000,
        4px -4px 0 #000,
        -4px 4px 0 #000,
        0 4px 0 #000,
        4px 0 0 #000,
        0 -4px 0 #000,
        -4px 0 0 #000,
        4px 4px 8px #000;
}

main .memeApp .memeRender .bottom {
    bottom: 0;
}

main .memeApp .memeRender .top {
    top: 0;
}

main .memeApp form {
   box-sizing: border-box;
   max-width: calc(85%);
   margin: 40px auto 50px;
}

main .memeApp form * {
    border-radius: 6px;
    margin-top: 15px;
}

main .memeApp form input {
    display: block;
    width: calc( 100% - 20px );
    font-size: 1.25rem;
    padding: 6px;
    border: 3px solid #000;
    text-transform: uppercase;
}

main .memeApp form button {
   width: 100%;
   text-align: center;
   outline: none;
   border: none;
   background: #000;
   color: #fff;
    font-size: 1.25rem;
    padding: 10px 0;
    cursor: pointer;
}